@import "./assets/fonts/pokemon_classic/stylesheet.css";
@import "./pages/Homepage/Homepage.css";
@import "./components/Gameboy/Gameboy.css";
@import "./components/Socials/Socials.css";
@import "./components/GBPlayer/GBPlayer.css";
@import "./components/GBScreen/GBScreen.css";
@import "./components/PokemonCries/PokemonCries.css";
@import "./pages/About/About.css";

* {
  margin: 0;
  padding: 0;
  text-decoration-line: none;
  list-style-type: none;
}

:root {
  --text-color-one: #717171;
  --text-color-two: #101010;
  --bg-color: #0a0a0a;
  --linear-bg-color: linear-gradient(
    180deg,
    rgba(135, 183, 255, 0) 0%,
    rgba(135, 183, 255, 0.1) 100%
  );
}

body {
  font-family: "Pokemon Classic";
  background: var(--bg-color);
  width: 100%;
  margin: auto;
}
